<template>
  <el-row>
    <el-col :span="24">
      <el-input v-model="bid" placeholder="Please input book id" />
      <el-button type="primary" @click="catchBook">Primary</el-button>
    </el-col>
  </el-row>
  <el-row>
    <el-col :span="24">
      <el-input type="textarea" v-model="book" />
    </el-col>
  </el-row>
</template>

<script>
import { ElMessage } from "element-plus";
import axios from "axios";
import $ from "jquery";
import cheerio from "cheerio";
import cont from "../config/cont";
import urls from "../config/urls";
export default {
  name: "Main",
  data() {
    return {
      bid: "",
      book: "",
    };
  },
  methods: {
    catchBook() {
      //68294240
      let bid = this.bid;
      if (bid == "") {
        ElMessage.error("Please input book id!");
        return;
      }
      this.catchFromXbqg(bid);
    },
    async catchFromXbqg(bid) {
      let host = "/xbqg";
      if(process.env.NODE_ENV == 'production'){
        host = urls[host];
      }
      let url = host + "/book/" + bid + "/";
      try {
        let listDom = await this.axiosGet(url);
        let startUrlDom = listDom(
          "body div.container.border3-2.mt8.mb20 > div.info-chapters.flex.flex-wrap > a:nth-child(1)"
        );
        if (!startUrlDom.length) {
          ElMessage.error("Start Url Not Found!");
          return;
        }
        let aurl = startUrlDom.attr("href");
        let content = "";
        while (aurl) {
          let pageDom = await this.axiosGet(host + aurl);
          aurl = null;
          if (!pageDom.length) {
            this.book += "Error: Page Not Found! [" + host + aurl + "]\n";
          }
          let nextUrlDom = pageDom("#next_url");
          if (nextUrlDom.length) {
            let nurl = nextUrlDom.attr("href");
            if (nurl.endsWith(".html")) {
              aurl = nurl;
            }
            let cid = nurl.replace(url, "").replace(".html", "");
            if (cid.indexOf("_") != -1) {
              let tDom = pageDom("#ss-reader-main > div.reader-main h1");
              if (tDom) {
                this.book += tDom.text() + "\n";
                content += "\n" + tDom.text() + "\n  ";
              }
            }

            let aDom = pageDom("#article");
            if (aDom) {
              var a = aDom.text();
              a = a.replace(/\n\s*?\n/g, "\n");
              a = a.replace(/(\n)\s+?(\S)/g, "$1  $2");
              a = a.replaceAll("(本章未完,请翻页)", "");
              a = a.replace(/第(.*?)页/g, "");
              content += a;
            }
          }
        }
        this.downloadFile(bid + ".txt", content);
      } catch (ex) {
        ElMessage.error("Error!");
        console.log(ex);
      }
    },
    findDom(dom, path) {
      let res = dom.find(path);
      if (res && res.length > 0) {
        return $(res[0]);
      }
      return null;
    },
    async axiosGet(url) {
      return new Promise((resolve, reject) => {
        axios
          .get(url, {
            proxy: cont.ips[Math.floor(Math.random() * 10)],
            headers: {
              "User-Agent": cont.userAgents[Math.floor(Math.random() * 5)],
            },
          })
          .then(function (resp) {
            let html = resp.data;
            if (html) {
              var dom = cheerio.load(html);
              resolve(dom);
            }
          })
          .catch(function (error) {
            reject(error);
          });
      });
    },
    downloadFile(fileName, content) {
      // 定义触发事件的DOM
      var aLink = document.createElement("a");
      // 定义BLOB对象，声明文件内容
      var blob = new Blob([content]);
      // 定义事件对象
      var evt = document.createEvent("MouseEvents");
      // 初始化事件
      evt.initEvent("click", false, false);
      // 定义下载文件名称
      aLink.download = fileName;
      // 根据上面定义的 BLOB 对象创建文件 dataURL
      aLink.href = URL.createObjectURL(blob);
      // 触发事件下载
      aLink.dispatchEvent(evt);
      // document.getElementById("article").appendChild(aLink);
    },
  },
};
</script>